.introduction {
  margin: 0px;
  padding: 0px;
  background-color: white;
  margin: 3em;
  padding: 2em;
  border: 1px solid black;

  h2,
  h3,
  h6,
  p {
    font-family: sans-serif;
  }

  h2 {
    font-size: 4em;
  }

  h3 {
    font-size: 3em;
  }

  h6 {
    font-size: 2em;
    margin-top: 0;
  }

  p {
    font-size: 2em;
  }

  hr {
    border: none;
    height: 0.2em;
    background-color: black;
  }
}
